import Head from 'next/head'

const Header = () => (
 
    <div>
      <Head>
        <title>Women's archive</title>
        <link rel="stylesheet" href="https://bootswatch.com/4/pulse/bootstrap.min.css"/>
       
        <link rel="stylesheet" href="https://unpkg.com/react-table@latest/react-table.css"/>
       
      </Head>
      <h5 style={{backgroundColor:"rgba(116, 116, 111, 0.359)",color:"green",width:"50%"}}>
      Australia Women's Archives Project</h5>
      <h1 className='display-4' style={{color:'#ffd633'}}>The Australian Women's Register</h1>
      
      <style jsx>{`
        div{
          position: relative;
          height: 5%;
          width: 80%;
          font-family: 'Cochin';
          margin-right: auto; 
          margin-left: auto;

        }
        h6{
          position:relative;
          color:purple
        }
      `}</style>
  </div>
  
);

export default Header;